@use "base/variables" as v;
@use "base/mixins" as m;

.alerta {
  width: 100%;
  background: v.$bgc;
  padding: 2rem;
  margin-top: 2rem;
  margin-bottom: 2rem;

  p {
    color: #FFF;
    font-size: 2rem;
    text-align: center;
  }
}

.ocultarAni {
  transition: ease padding 2s, height 2s;

  p {
    transition: height 2s;
    display: none;
  }

  padding: 0;
  height: 0;
}
