@use "variables" as v;
@use "mixins" as m;

* {
  margin: 0;
  padding: 0;
  font-size: 62.5%;
  font-family: "Quicksand", sans-serif;
  box-sizing: border-box;
}

.hr{
  background-color: v.$bgc;
  height: .1rem;
  width: 80%;
  margin: 0 auto;
  margin-top: 5rem;

  @include m.desktopXL {
    width: 70%;
  }
}

.mostrar {
  display: block;
}
