@use "base/variables" as v;
@use "base/mixins" as m;

.proyectos {
  width: 80%;
  margin: 10rem auto;

  h1 {
    font-size: v.$titulo;
    margin-bottom: 2rem;
  }
}

.contenedor-proyecto {
  border: 0.01px solid rgba(0, 0, 0, 70%);
  border-radius: 5px;
  padding: 1rem 0;
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;

  img {
    margin: 0 auto;
    width: 90%;
    height: 150px;
    border-radius: 0.5rem;
  }

  a,
  h3,
  p {
    width: 80%;
    font-size: v.$parrafo;
    margin: 1rem auto;
  }

  a,
  h3 {
    width: auto;
    color: #000;
  }
}

@include m.tablet {
  .proyectos {
    h1 {
      font-size: 4rem;
    }

    div {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 4rem;

      .contenedor-proyecto {
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        width: 100%;

        img {
          width: 95%;
        }

        a,
        h3,
        p {
          font-size: 1.8rem;
          margin: 0 auto;
        }
      }
    }
  }
}

@include m.desktop {
  .proyectos {
    div {
      grid-template-columns: repeat(2, 1fr);

      .contenedor-proyecto {
        width: 80%;
      }
    }
  }
}

@include m.desktopL {
  .proyectos {
    width: 60%;
    div {
      grid-template-columns: repeat(3, 1fr);
      width: 100%;

      .contenedor-proyecto {
        width: 100%;
      }
    }
  }
}

