@use "base/variables" as v;
@use "base/mixins" as m;

.admin {
  width: 100%;
  margin: 2rem auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  h1 {
    font-size: v.$titulo;
    margin-bottom: 2rem;
  }

  table {
    width: 100%;
    th {
      font-size: 1.1rem;
    }

    td {
      font-size: 1rem;
      text-align: center;
    }
  }

  form {
    display: flex;
    flex-direction: column;
    width: 80%;

    label,
    input {
      font-size: v.$titulo;
    }

    input {
      &:last-of-type {
        width: 50%;
        margin: 0 auto;
        margin-bottom: 2rem;
      }
      margin-bottom: 2rem;
    }
  }

  a {
    font-size: v.$parrafo;
    text-decoration: none;

    &:hover {
      cursor: pointer;
      text-decoration: underline;
    }
  }

  @include m.tablet {
    margin-top: 10rem;

    h1 {
      font-size: 4rem;
    }

    table {
      th {
        font-size: 1.5rem;
      }

      td {
        font-size: 1.3rem;
      }
    }

    form {
      input {
        &:last-of-type {
          width: 30%;
        }
      }
    }
  }

  @include m.desktopXL {
    width: 80%;

    table {
      th {
        font-size: 2rem;
      }

      td {
        font-size: 1.7rem;
      }
    }
  }
}
