/** Media Queries **/
.banner {
  margin: 5rem 0;
  background-image: url("/build/img/bannerTel.webp");
  background-position: center center;
  background-repeat: no-repeat;
  height: 15rem;
  position: relative;
}

.banner .contenido-banner {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}

.banner .contenido-banner p {
  color: #fff;
  font-size: 2rem;
}

@media (min-width: 768px) {
  .banner {
    height: 30rem;
    background-size: cover;
    background-image: url("/build/img/banner.webp");
    background-position: bottom center;
  }
  .banner .contenido-banner p {
    font-size: 3rem;
  }
}
@media (min-width: 1024px) {
  .banner {
    height: 45rem;
  }
  .banner .contenido-banner {
    background-color: rgba(0, 0, 0, 0.4);
  }
}
@media (min-width: 1440px) {
  .banner {
    height: 60rem;
  }
  .banner .contenido-banner {
    background-color: rgba(0, 0, 0, 0.5);
  }
}
@media (min-width: 1440px) {
  .banner {
    height: 60rem;
    background-position: top;
  }
  .banner .contenido-banner {
    background-color: rgba(0, 0, 0, 0.5);
  }
}
header {
  background-color: #2e266d;
  height: auto;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

header a {
  text-decoration: none;
}

header a h1 {
  font-size: 3rem;
  color: #fff;
}

header .menu {
  margin: 0 2rem;
  margin-top: 2rem;
  height: 32px;
  width: 32px;
  background-image: url("/build/img/menuB.webp");
}

header .nav {
  display: none;
  margin: 0.5rem 2rem;
}

header .nav a {
  margin-top: 0.5rem;
  color: #fff;
  text-decoration: none;
  text-align: left;
  font-size: 1.6rem;
  animation-duration: 2s;
  animation-name: slidein;
}

header .mostrar {
  display: flex;
  flex-direction: column;
  text-align: left;
}

@keyframes slidein {
  from {
    margin-left: 100%;
    width: 300%;
  }
  to {
    margin-left: 0%;
    width: 100%;
  }
}
@media (min-width: 768px) {
  header {
    justify-content: space-evenly;
    align-items: center;
  }
  header a {
    order: -1;
  }
  header a h1 {
    font-size: 4rem;
    position: static;
  }
  header .menu {
    display: none;
  }
  header .nav {
    display: flex;
    justify-content: space-between;
    width: 110%;
  }
  header .nav a {
    transition: slidein2 2s;
    font-size: 2rem;
  }
  header .nav a:hover {
    text-decoration: underline;
    cursor: pointer;
  }
  @keyframes slidein2 {
    from {
      width: 200%;
    }
    to {
      width: 100%;
    }
  }
}
footer {
  height: 10rem;
  background-color: #2e266d;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

footer p {
  color: #fff;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

footer p:last-of-type {
  order: -1;
}

@media (min-width: 768px) {
  footer {
    flex-direction: row;
    justify-content: space-around;
  }
  footer p:last-of-type {
    order: -1;
  }
}
@media (min-width: 1024px) {
  footer {
    justify-content: space-between;
  }
}
.alerta {
  width: 100%;
  background: #2e266d;
  padding: 2rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.alerta p {
  color: #FFF;
  font-size: 2rem;
  text-align: center;
}

.ocultarAni {
  transition: ease padding 2s, height 2s;
  padding: 0;
  height: 0;
}

.ocultarAni p {
  transition: height 2s;
  display: none;
}

/** Media Queries **/
.resumen {
  width: 80%;
  margin: 2rem auto;
}

.resumen h1 {
  font-size: 3rem;
  margin-bottom: 2rem;
}

.resumen p {
  font-size: 1.5rem;
  color: #000;
}

.resumen a {
  font-size: 1.5rem;
  text-decoration: none;
}

.resumen a:hover {
  cursor: pointer;
  text-decoration: underline;
}

.resumen span {
  font-size: 1.5rem;
  font-weight: 700;
}

.resumen div img {
  display: none;
}

@media (min-width: 768px) {
  .resumen {
    margin-top: 10rem;
  }
  .resumen h1 {
    font-size: 4rem;
  }
  .resumen p {
    font-size: 2rem;
  }
  .resumen span {
    font-size: 2rem;
  }
  .resumen div {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
  .resumen div img {
    display: flex;
    width: 25rem;
    border-radius: 2rem;
  }
}
@media (min-width: 1440px) {
  .resumen {
    width: 60%;
  }
}
.tecnologias {
  width: 80%;
  margin: 10rem auto;
}

.tecnologias h1 {
  font-size: 3rem;
  margin-bottom: 2rem;
}

.tecnologias p {
  font-size: 2rem;
  margin-bottom: 2rem;
}

.tecnologias label {
  font-size: 1.8rem;
}

.tecnologias progress {
  height: 3rem;
  width: 100%;
  margin-bottom: 1rem;
}

@media (min-width: 768px) {
  .tecnologias {
    margin-top: 10rem;
  }
  .tecnologias h1 {
    font-size: 4rem;
  }
}
@media (min-width: 1440px) {
  .tecnologias progress {
    height: 3rem;
    width: 100%;
    margin-bottom: 1rem;
  }
}
@media (min-width: 1440px) {
  .tecnologias {
    width: 60%;
  }
}
.proyectos {
  width: 80%;
  margin: 10rem auto;
}

.proyectos h1 {
  font-size: 3rem;
  margin-bottom: 2rem;
}

.contenedor-proyecto {
  border: 0.01px solid rgba(0, 0, 0, 0.7);
  border-radius: 5px;
  padding: 1rem 0;
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}

.contenedor-proyecto img {
  margin: 0 auto;
  width: 90%;
  height: 150px;
  border-radius: 0.5rem;
}

.contenedor-proyecto a,
.contenedor-proyecto h3,
.contenedor-proyecto p {
  width: 80%;
  font-size: 1.5rem;
  margin: 1rem auto;
}

.contenedor-proyecto a,
.contenedor-proyecto h3 {
  width: auto;
  color: #000;
}

@media (min-width: 768px) {
  .proyectos h1 {
    font-size: 4rem;
  }
  .proyectos div {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
  .proyectos div .contenedor-proyecto {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  .proyectos div .contenedor-proyecto img {
    width: 95%;
  }
  .proyectos div .contenedor-proyecto a,
.proyectos div .contenedor-proyecto h3,
.proyectos div .contenedor-proyecto p {
    font-size: 1.8rem;
    margin: 0 auto;
  }
}
@media (min-width: 1024px) {
  .proyectos div {
    grid-template-columns: repeat(2, 1fr);
  }
  .proyectos div .contenedor-proyecto {
    width: 80%;
  }
}
@media (min-width: 1440px) {
  .proyectos {
    width: 60%;
  }
  .proyectos div {
    grid-template-columns: repeat(3, 1fr);
    width: 100%;
  }
  .proyectos div .contenedor-proyecto {
    width: 100%;
  }
}
.contacto {
  width: 80%;
  margin: 10rem auto;
}

.contacto h1 {
  font-size: 3rem;
  margin-bottom: 2rem;
}

.contacto form {
  display: flex;
  flex-direction: column;
  font-size: 2rem;
  border: 1px solid gray;
  border-radius: 20px;
}

.contacto form label {
  margin-top: 2rem;
  margin-left: 2.3rem;
}

.contacto form input {
  width: 80%;
  margin: 0 auto;
}

.contacto form div {
  display: flex;
  width: 80%;
  margin: 1rem auto;
  font-size: 2rem;
}

.contacto form div label {
  width: 100%;
  margin: 0 1rem;
}

.contacto form div input {
  width: 100%;
}

.contacto form .contactoOp {
  display: flex;
  flex-direction: column;
}

.contacto form .contactoOp p,
.contacto form .contactoOp label {
  margin-top: 1rem;
}

.contacto form .sub {
  margin: 2rem auto;
  width: 50%;
  height: 3rem;
  border: 1px solid black;
  border-radius: 10px;
}

.contacto form .sub:hover {
  cursor: pointer;
  box-shadow: 5px 5px 15px gray;
}

@media (min-width: 768px) {
  .contacto h1 {
    font-size: 4rem;
  }
  .contacto form label {
    font-size: 2.3rem;
    margin-top: 3rem;
    margin-left: 6rem;
  }
  .contacto form input {
    font-size: 2.5rem;
  }
  .contacto form div {
    display: flex;
    align-items: center;
    font-size: 2.5rem;
  }
}
@media (min-width: 1024px) {
  .contacto form label {
    margin-left: 8rem;
  }
  .contacto form .sub {
    width: 20%;
  }
}
@media (min-width: 1440px) {
  .contacto form label {
    margin-left: 12rem;
  }
}
@media (min-width: 1440px) {
  .contacto {
    width: 60%;
  }
  .contacto form label {
    margin-left: 12rem;
  }
}
.admin {
  width: 100%;
  margin: 2rem auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.admin h1 {
  font-size: 3rem;
  margin-bottom: 2rem;
}

.admin table {
  width: 100%;
}

.admin table th {
  font-size: 1.1rem;
}

.admin table td {
  font-size: 1rem;
  text-align: center;
}

.admin form {
  display: flex;
  flex-direction: column;
  width: 80%;
}

.admin form label,
.admin form input {
  font-size: 3rem;
}

.admin form input {
  margin-bottom: 2rem;
}

.admin form input:last-of-type {
  width: 50%;
  margin: 0 auto;
  margin-bottom: 2rem;
}

.admin a {
  font-size: 1.5rem;
  text-decoration: none;
}

.admin a:hover {
  cursor: pointer;
  text-decoration: underline;
}

@media (min-width: 768px) {
  .admin {
    margin-top: 10rem;
  }
  .admin h1 {
    font-size: 4rem;
  }
  .admin table th {
    font-size: 1.5rem;
  }
  .admin table td {
    font-size: 1.3rem;
  }
  .admin form input:last-of-type {
    width: 30%;
  }
}
@media (min-width: 1440px) {
  .admin {
    width: 80%;
  }
  .admin table th {
    font-size: 2rem;
  }
  .admin table td {
    font-size: 1.7rem;
  }
}
/** Media Queries **/
* {
  margin: 0;
  padding: 0;
  font-size: 62.5%;
  font-family: "Quicksand", sans-serif;
  box-sizing: border-box;
}

.hr {
  background-color: #2e266d;
  height: 0.1rem;
  width: 80%;
  margin: 0 auto;
  margin-top: 5rem;
}

@media (min-width: 1440px) {
  .hr {
    width: 70%;
  }
}
.mostrar {
  display: block;
}

