@use "base/variables" as v;
@use "base/mixins" as m;

.contacto {
  width: 80%;
  margin: 10rem auto;

  h1 {
    font-size: v.$titulo;
    margin-bottom: 2rem;
  }

  form {
    display: flex;
    flex-direction: column;
    font-size: v.$banner;
    border: 1px solid gray;
    border-radius: 20px;

    label {
      margin-top: 2rem;
      margin-left: 2.3rem;
    }

    input {
      width: 80%;
      margin: 0 auto;
    }

    div {
      display: flex;
      width: 80%;
      margin: 1rem auto;
      font-size: v.$banner;

      label {
        width: 100%;
        margin: 0 1rem;
      }

      input {
        width: 100%;
      }
    }

    .contactoOp {
      display: flex;
      flex-direction: column;

      p,
      label {
        margin-top: 1rem;
      }
    }

    .sub {
      margin: 2rem auto;
      width: 50%;
      height: 3rem;
      border: 1px solid black;
      border-radius: 10px;
    }

    .sub:hover {
      cursor: pointer;
      box-shadow: 5px 5px 15px gray;
    }
  }

  @include m.tablet {
    h1 {
      font-size: 4rem;
    }

    form {
      label {
        font-size: 2.3rem;
        margin-top: 3rem;
        margin-left: 6rem;
      }

      input {
        font-size: 2.5rem;
      }

      div {
        display: flex;
        align-items: center;
        font-size: 2.5rem;
      }
    }
  }

  @include m.desktop {
    form {
      label {
        margin-left: 8rem;
      }

      .sub {      
        width: 20%;        
      }
    }
  }

  @include m.desktopL {
    form {
      label {
        margin-left: 12rem;
      }
    }
  }

  @include m.desktopXL {
    width: 60%;
    form {
      label {
        margin-left: 12rem;
      }
    }
  }
}
