@use "base/variables" as v;
@use "base/mixins" as m;

.resumen {
  width: 80%;
  margin: 2rem auto;

  h1 {
    font-size: v.$titulo;
    margin-bottom: 2rem;
  }

  p {
    font-size: v.$parrafo;
    color: v.$negro;
  }

  a {
    font-size: v.$parrafo;
    text-decoration: none;

    &:hover {
      cursor: pointer;
      text-decoration: underline;
    }
  }

  span {
    font-size: v.$parrafo;
    font-weight: v.$bold;
  }

  div {
    img {
      display: none;
    }
  }

  @include m.tablet {
    margin-top: 10rem;

    h1 {
      font-size: 4rem;
    }

    p {
      font-size: 2rem;
    }

    span {
      font-size: 2rem;
    }

    div {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-items: center;

      img {
        display: flex;
        width: 25rem;
        border-radius: 2rem;
      }
    }
  }

  @include m.desktopXL {
    width: 60%;
  }
}
