@use "base/mixins" as m;
@use "base/variables" as v;

.tecnologias {
  width: 80%;
  margin: 10rem auto;

  h1 {
    font-size: v.$titulo;
    margin-bottom: 2rem;
  }

  p {
    font-size: 2rem;
    margin-bottom: 2rem;
  }

  label {
    font-size: 1.8rem;
  }

  progress {
    height: 3rem;
    width: 100%;
    margin-bottom: 1rem;
  }

  @include m.tablet {
    margin-top: 10rem;

    h1 {
      font-size: 4rem;
    }
  }

  @include m.desktopL {
    progress {
      height: 3rem;
      width: 100%;
      margin-bottom: 1rem;
    }
  }

  @include m.desktopXL {
    width: 60%;      
  }
}
