@use "base/variables" as v;
@use "base/mixins" as m;

footer {
  height: 10rem;
  background-color: v.$bgc;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  p {
    color: #fff;
    font-size: 1.5rem;
    margin-bottom: 1rem;

    &:last-of-type {
      order: -1;
    }
  }

  @include m.tablet {
    flex-direction: row;
    justify-content: space-around;

    p {
      &:last-of-type {
        order: -1;        
      }
    }
  }

  @include m.desktop {
    justify-content: space-between;
  }
}
