@use "base/variables" as v;
@use "base/mixins" as m;

.banner {
  margin: 5rem 0;
  background-image: url("/build/img/bannerTel.webp");
  background-position: center center;
  background-repeat: no-repeat;
  height: 15rem;
  position: relative;

  .contenido-banner {
    position: absolute;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 50%);

    p {
      color: v.$blanco;
      font-size: v.$banner;
    }
  }

  @include m.tablet {
    height: 30rem;
    background-size: cover;
    background-image: url("/build/img/banner.webp");
    background-position: bottom center;

    .contenido-banner {
      p {
        font-size: v.$titulo;
      }
    }
  }

  @include m.desktop {
    height: 45rem;

    .contenido-banner {
      background-color: rgba(0, 0, 0, 40%);
    }
  }

  @include m.desktopL {
    height: 60rem;

    .contenido-banner {
      background-color: rgba(0, 0, 0, 50%);
    }
  }

  @include m.desktopXL {
    height: 60rem;
    background-position: top;

    .contenido-banner {
      background-color: rgba(0, 0, 0, 50%);
    }
  }
}
