@use "base/mixins" as m;
@use "base/variables" as v;

header {
  background-color: v.$bgc;
  height: auto;
  width: 100%;
  display: flex;
  justify-content: space-between;

  a {
    text-decoration: none;

    h1 {
      font-size: v.$titulo;
      color: v.$blanco;
    }
  }

  .menu {
    margin: 0 2rem;
    margin-top: 2rem;
    height: 32px;
    width: 32px;
    background-image: url("/build/img/menuB.webp");
  }

  .nav {
    display: none;
    margin: 0.5rem 2rem;

    a {
      margin-top: 0.5rem;
      color: v.$blanco;
      text-decoration: none;
      text-align: left;
      font-size: 1.6rem;
      animation-duration: 2s;
      animation-name: slidein;
    }
  }

  .mostrar {
    display: flex;
    flex-direction: column;
    text-align: left;
  }

  @keyframes slidein {
    from {
      margin-left: 100%;
      width: 300%;
    }

    to {
      margin-left: 0%;
      width: 100%;
    }
  }

  @include m.tablet {
    justify-content: space-evenly;
    align-items: center;

    a {
      order: -1;
      h1 {
        font-size: 4rem;
        position: static;
      }
    }

    .menu {
      display: none;
    }

    .nav {
      display: flex;
      justify-content: space-between;
      width: 110%;

      a {
        transition: slidein2 2s;
        font-size: 2rem;

        &:hover {
          text-decoration: underline;
          cursor: pointer;
        }
      }

      @keyframes slidein2 {
        from {
          width: 200%;
        }

        to {
          width: 100%;
        }
      }
    }
  }
}
